import React from 'react';
import './arrugas.css'

import { Link } from 'react-scroll';

import bg_img from '../../../../assets/img/bg_blefaroplastia.jpg';
import { Overlay, Logo, Navbar, NavbarFull, Footer, NavDotsFacial } from '../../../../components';

const Arrugas = () => {
  return (
    <div className='info__arrugas'>
      <div>
        <Navbar />
        {/* <NavbarFull /> */}
        <Logo />
        
        <div className='content__overlay__arrugas'>
          <div className='content__container__arrugas'>
            <div className='content__info__arrugas'>
              <h1>Arrugas de expresin</h1>
              <p>
                Las arrugas de expresin son aquellas que aparecen con la mmica facial
                (gesticulacin) y se relacionan con la expresividad de la persona. Se
                originan por la contraccin de los msculos voluntarios de la cara a partir
                de los impulsos nerviosos que desde el cerebro se conducen a travs del
                llamado nervio facial. 
                <br /><br />
                La contraccin de cada msculo genera una arruga que le es propia. As:
                <br /><br />
                <ul>
                  <li>
                    La contraccin del msculo frontal provoca la elevacin de las cejas y
                    aparecen arrugas que recorren horizontalmente la frente
                  </li>
                  <li>
                    La contraccin del msculo corrugador de la zona glabelar provoca el
                    fruncimiento del entrecejo (aproxima entre s las cejas) y provocan la
                    aparicin de arrugas verticales en esta zona
                  </li>
                  <li>
                    La contraccin del msculo orbicular de los prpados causa el cierre
                    forzado de los ojos y genera la aparicin de unas arrugas que irradian
                    desde el canto lateral de los mismos. Son las conocidas popularmente como
                    "patas de gallo"
                  </li>
                  <li>
                    La contraccin del msculo orbicular de los labios causa el fruncimiento
                    de la boca y provoca la aparicin del famoso "cdigo de barras"
                  </li>
                  <li>
                    La contraccin del msculo platisma provoca la aparicin de unas bandas
                    verticales en la parte anterior del cuello que discurren desde el reborde
                    inferior de la mandbula hacia el esternn y las clavculas
                  </li>
                </ul>
              </p>

              <h2>Cundo se indica el tratamiento de las arrugas de expresin?</h2>
              <p>
                Aunque todas las personas expresan arrugas de expresin con la gesticulacin,
                slo debe indicarse su tratamiento por motivos estticos cuando la intensidad
                de las arrugas supone un importante impacto psicolgico en el paciente. 
                <br /><br />
                Solamente est autorizado el tratamiento de aquellas arrugas localizadas en el
                tercio superior de la cara, concretamente las localizadas en la frente, el
                entrecejo y las "patas de gallo".
              </p>

              <h2>En qu consiste el tratamiento de las arrugas de expresin?</h2>
              <p>
                El tratamiento se hace en la consulta y de forma ambulatoria.
                <br /><br />
                Se identifica el msculo responsable de generar la arruga y se bloquea por
                infiltracin con aguja fina. Habitualmente practicamos esta intervencin
                sin necesidad de anestesia local. En pacientes ms sensibles puede aplicarse
                previamente analgesia tpica mediante la aplicacin de fro en la zona a tratar.
                <br /><br />
                La infiltracin debilita el normal funcionamiento del msculo, que pierde
                fuerza para contraerse. Este efecto no es apreciable de forma inmediata sino
                que empieza a manifestarse a partir del tercer da y son mximos al mes y
                medio del tratamiento.
              </p>

              <h2>Contraindicaciones</h2>
              <p>
                En la primera consulta el cirujano realizar una historia clnica completa,
                descartando enfermedades generales o medicaciones que est tomando que pudieran
                interferir o incluso contraindicar el tratamiento.
                <br /><br />
                El tratamiento est contraindicado en:
                <br /><br />
                <ul>
                  <li>
                    Pacientes menores de 18 aos.
                  </li>
                  <li>
                    Sensibilidad conocida a algunos de sus componentes.
                  </li>
                  <li>
                    Si existen enfermedades de la coagulacin sangunea como la hemofilia o
                    se estn tomando medicamentos que la alteran (cido acetilsaliclico
                    -Aspirina - , heparina, Sintrom, Plavix  y otros).
                  </li>
                  <li>
                    Antecedentes de enfermedades del sistema neuromuscular que impliquen
                    debilidad en la contraccin de los msculos tales como la Miastenia
                    gravis, el Sndrome de Eaton-Lambert, esclerosis lateral amioatrfica
                    (ELA) y otras.
                  </li>
                  <li>
                    Infecciones activas de la piel en el lugar a tratar.
                  </li>
                  <li>
                    Antecedentes de enfermedades conocidas del sistema inmunitario en fase
                    activa o pacientes con tratamiento farmacolgico inmunosupresor.
                  </li>
                </ul>
                <br /><br />
                Se desconocen las posibles interacciones de este producto con:
                <br /><br />
                <ul>
                  <li>
                    El embarazo y lactancia materna. Por ello, se desaconseja su
                    administracin a mujeres gestantes y durante la lactancia materna.
                  </li>
                  <li>
                    Otros implantes cutneos que el paciente pudiera tener previamente
                    aplicados en el rea a tratar. No se aconseja, por tanto, mezclar
                    productos en la misma zona.
                  </li>
                </ul>
              </p>

              <h2>Limitaciones del tratamiento</h2>
              <p>
                Con el paso del tiempo, el tratamiento aplicado pierde efectividad y se
                apreciar una lenta y progresiva prdida del efecto conseguido. Aunque la
                contraccin muscular empieza a recuperarse a partir de los 3 meses de su
                aplicacin, sus efectos pueden an notarse hasta aproximadamente los 6 meses
                del tratamiento.
                <br /><br />
                Por ello, siempre son necesarias de forma peridica nuevos tratamientos para
                mantener los resultados. No se aconsejan nuevas aplicaciones antes de los 3
                meses, siendo lo recomendable repetirlas cada 6 meses.
                <br /><br />
                Una aplicacin demasiado frecuente o excesiva puede dar lugar a una disminucin
                en la efectividad de futuros tratamientos.
              </p>
            </div>
          </div>
        </div>

        <div className='footer__info__arrugas'>
          <div className='footer__info_site__arrugas'>
              <ul>
                  <li>
                      <a href="/aviso-legal" className='underline'>Aviso Legal</a>
                  </li>
                  <li>-</li>
                  <li>
                      <a href="/politica-privacidad" className='underline'>Poltica de Privacidad</a>
                  </li>
                  <li>-</li>
                  <li>
                      <a href="/creditos" className='underline'>Crditos</a>
                  </li>
              </ul>
          </div>
        </div>

        <img className='bg__content_info' src={bg_img} />
      </div>
    </div>
  )
}

export default Arrugas