            <div className='content__info__cicatriz'>
              <h1>La cicatriz</h1>
              <p>
                Cuando un tejido vivo sufre una agresin y es lesionado, se genera una respuesta
                biolgica cuyo objetivo es la curacin de dicha lesin y su consecuencia es la
                aparicin de una cicatriz.
                <br /><br />
                En realidad, la capacidad de regeneracin del cuerpo humano es ms bien limitada
                y supone que el nuevo tejido cicatricial sea diferente del original. Por ello:
                <br /><br />
                <ul>
                  <li>La cicatriz ocasiona una marca permanente</li>
                  <li>Se pierden las propiedades del tejido original lesionado</li>
                </ul>
                <br /><br />
                La cicatriz puede ser lineal (Ejemplo: tras un corte en la piel) o abarcar una
                superficie (Ejemplo: tras una quemadura).
              </p>

              <h2>Fases de la cicatrizacin</h2>
              <p>
                Despus de una herida se pone en marcha un complejo proceso de reparacin tisular en
                el que pueden distinguirse varias fases:
                <br /><br />
                <ol>
                  <li>
                    <b>Fase de respuesta inflamatoria aguda:</b> su objetivo es limpiar la herida.
                    <br /><br />
                    Los vasos sanguneos de la zona se dilatan en su calibre y llegan a la herida las
                    clulas de la inflamacin (glbulos blancos y macrfagos) que van a eliminar los
                    tejidos lesionados y luchar contra posibles bacterias que pudieran haber
                    contaminado la zona.
                  </li>
                  <br /><br />
                  <li>
                    <b>Fase de proliferacin celular:</b> su objetivo es curar la herida gracias a la
                    multiplicacin de una serie de clulas que irn formando un nuevo tejido llamado
                    tejido cicatricial o, simplemente, cicatriz.
                    <br /><br />
                    Una vez limpia la herida, el defecto tisular ocasionado se va a rellenar
                    fundamentalmente de fibras de colgeno, sintetizadas a partir de unas clulas
                    llamadas fibroblastos. El colgeno ser el componente principal de la cicatriz y,
                    en esta fase, se dispone de forma anrquica, como un ovillo de fibras desordenado.
                    <br /><br />
                    Paralelamente, el tamao de la herida va a ir reducindose gracias a la contraccin
                    generada por otro tipo especial de clulas llamadas miofibroblastos.
                    <br /><br />
                    Y, al final, por el llamado proceso de epitelizacin, las clulas epiteliales
                    presentes en los bordes de la herida van a multiplicarse y migrar hacia el centro
                    para as cerrar definitivamente la herida, completndose el proceso propiamente
                    dicho de la curacin.
                    <br /><br />
                    La fase de proliferacin celular exige mejorar la irrigacin de la zona mediante
                    la formacin de nuevos vasos sanguneos por multiplicacin de las llamadas clulas
                    endoteliales. Su objetivo es favorecer el aporte de nutrientes y oxgeno que
                    garanticen el proceso de la curacin de la herida.
                  </li>
                  <br /><br />
                  <li>
                    <b>Fase de maduracin:</b> su objetivo principal es remodelar la cicatriz. Este
                    proceso puede durar ms de un ao.
                    <br /><br />
                    Con la curacin de la herida y la formacin de la cicatriz, muchas de las clulas
                    que haban proliferado ya han cumplido su objetivo y van a disminuir en nmero y
                    las que no hacen falta acaban desapareciendo.
                    <br /><br />
                    Ahora se inicia un lento proceso de remodelado de la cicatriz. De forma paulatina,
                    parte del colgeno depositado de forma anrquica en la fase proliferativa se va a
                    eliminar y un nuevo colgeno se va a sintetizar para disponerse ya de forma ms
                    ordenada, formando haces paralelos entre s.
                  </li>
                </ol>
              </p>

              <h2>Cmo evoluciona una cicatriz con el tiempo?</h2>
              <p>
                El aspecto de una cicatriz no es siempre igual y est condicionado por la fase
                evolutiva en la que se encuentra.
                <br /><br />
                Al principio, durante la fase de proliferacin, las cicatrices manifiestan el
                aumento de irrigacin sangunea y el depsito de colgeno. El mayor aporte sanguneo
                explica el color rojo que toma la cicatriz en este perodo y la presencia de picor
                en la zona. Los depsitos de colgeno son los responsables que la cicatriz aparezca
                endurecida al tacto, engrosada y con falta de flexibilidad, notndose tirantez con
                los movimientos. La anarqua en la distribucin de las fibras de colgeno ocasiona
                que la cicatriz no resista adecuadamente las fuerzas de tensin cutneas, sobre todo
                en zonas de movimiento (hombros, rodillas), por lo que con el paso del tiempo pueden
                ir ensanchndose en estas zonas. 
                <br /><br />
                Durante la fase de maduracin, en cambio, las cicatrices van perdiendo el color rojo
                y dejan de picar pues disminuye su irrigacin sangunea. Adems, se aplanan, se
                ablandan y se relaja su tirantez, mejorando su flexibilidad, cuando se remodela la
                disposicin de las fibras de colgeno.
                <br /><br />
                Una cicatriz puede tardar en madurar un mnimo de un ao pero, en ocasiones, puede
                llevar mucho ms tiempo.
              </p>

              <h2>La cicatrizacin anmala</h2>
              <p>
                Acabada la fase de maduracin, la cicatriz ideal debe ser indolora, quedar de color
                blanco nacarado, ser estrecha, estar a nivel del resto de la piel y ser lo
                suficientemente elstica como para no causar limitacin en los movimientos de las
                articulaciones vecinas. Tampoco debe deformar la anatoma regional.

                Por desgracia, muchas veces vemos procesos cicatriciales anmalos. El abanico de
                posibilidades es muy amplio:
                <br /><br />
                <ul>
                  <li>Cicatrices dolorosas</li>
                  <li>
                    Cicatrices con cambios de color, tanto por defecto (hipopigmentacin) como por
                    exceso (hiperpigmentacin)
                  </li>
                  <li>Cicatrices dilatadas (diastasadas)</li>
                  <li>
                    Cicatrices deprimidas (hundidas respecto a la piel vecina) o abultadas
                    (sobreelevadas)
                  </li>
                  <li>
                    Cicatrices retrctiles, que limitan el movimiento de las articulaciones o que
                    distorsionan la anatoma local
                  </li>
                  <li>
                    Cicatrices exageradas. Se llama cicatriz hipertrfica cuando aparece engrosada,
                    dura y de un color rojo intenso. Se llama cicatriz queloidea o, simplemente,
                    queloide, a una cicatriz que crece descontroladamente recordando a un proceso
                    tumoral y que se extiende caractersticamente incluso ms all de los lmites
                    de la herida original.
                  </li>
                </ul>
                
                
              </p>

              <h2>Factores que causan una cicatrizacin anmala</h2>
              <p>
                Son muchos los factores que pueden influir a lo largo del proceso de cicatrizacin
                y que pueden condicionar el resultado.
                <br /><br />
                Repercuten negativamente en la cicatrizacin:
                <br /><br />
                <ul>
                  <li>
                    La edad, pues enlentece el metabolismo celular y todos los procesos de reparacin
                    celulares
                  </li>
                  <li>
                    La malnutricin, porque dificulta la proliferacin celular y la sntesis del
                    colgeno. Al respecto, son imprescindibles las protenas, los cidos grasos y
                    de diversas vitaminas (A, C) y oligoelementos (cinc, cobre)
                  </li>
                  <li>
                    Problemas de irrigacin sangunea locales, pues bloquearn la llegada de
                    nutrientes y oxgeno a la herida. Sus causas principales son la nicotina del
                    tabaco, la diabetes mellitus y la arteriosclerosis y la aplicacin previa en
                    la zona de radioterapia
                  </li>
                  <li>
                    Los tratamientos crnicos con corticoides y las personas inmunosuprimidas, pues
                    tienen mermada la respuesta inflamatoria de la cicatrizacin
                  </li>
                  <li>
                    El sol, pues las radiaciones ultravioleta estimulan la sntesis de melanina en
                    los tejidos inflamados y causan pigmentacin oscura de la cicatriz
                  </li>
                  <li>
                    Determinadas localizaciones. La zona del esternn, los hombros y la espalda tienen
                    ms riesgo de hacer cicatrices exageradas. En las rodillas y codos las cicatrices
                    tienden a ensancharse. Las cicatrices que son paralelas a los pliegues naturales
                    de la piel, en cambio, se disimulan mejor (Ejemplo: cicatriz horizontal en la
                    frente)
                  </li>
                </ul>
              </p>

              <h2>Cmo favorecer una cicatrizacin ptima?</h2>
              <p>
                En las cirugas electivas el cirujano, siempre que el caso lo permita, puede escoger
                la localizacin y el tamao de la incisin. ste es un buen punto de partida para
                conseguir una ptima cicatrizacin. En cambio, en los accidentes no podemos controlar
                esta cuestin.
                <br /><br />
                Ni que decir tiene que el tratamiento de la herida en la fase aguda es muy importante. Se debe limpiar de cuerpos extraos y la tcnica de sutura debe ser meticulosa.
                <br /><br />
                En la fase inicial debemos hacer reposo de la zona afectada, evitando el sangrado de
                la herida y tensiones en la sutura. El uso diario de antispticos como la povidona
                yodada luchar contra la infeccin. Si hay puntos de sutura externos, se debern
                extraer lo antes posible para evitar marcas cutneas.
                <br /><br />
                Durante la fase inflamatoria se debe evitar el sol y el calor sobre la cicatriz.
                Pueden ser necesarias prendas de compresin elsticas para aliviar el disconfort.
                <br /><br />
                Durante la fase de maduracin se debe llegar a un equilibrio entre el reinicio de su
                actividad fsica habitual y el cuidado de la cicatriz. Son aconsejables la aplicacin
                de cremas hidratantes pues la piel cicatricial tiende a la sequedad por falta del
                sebo natural. Para mejorar su elasticidad pueden aplicarse masajes manuales. A veces
                pueden ser necesarios ejercicios de rehabilitacin y fisioterapia si la cicatriz
                dificulta el movimiento de las articulaciones vecinas. La proteccin solar debe
                prolongarse tambin en esta fase, hasta que se complete la maduracin de la cicatriz.
              </p>

              <h2>Qu hacer frente a un mal resultado cicatricial?</h2>
              <p>
                Cuando un paciente consulta por un mal resultado cicatricial, se recopilar
                informacin suficiente acerca del motivo de la cicatriz y el tratamiento realizado.
                Es importante detectar si hubo problemas surgidos en la fase aguda que pudieran
                explicar el porqu de dicho mal resultado. Tambin recogeremos qu cuidados se
                hicieron hasta que la maduracin de la cicatriz. Se deben identificar posibles
                problemas mdicos de base que condicionen la cicatrizacin.
                <br /><br />
                Durante la exploracin fsica analizaremos la localizacin, el tamao y el tipo de
                cicatrizacin conseguida. Identificaremos si el problema es simplemente esttico o
                presenta repercusiones funcionales (limitacin del movimiento articular o
                deformidades), pues estos ltimos siempre sern prioritarios a solucionar.
                <br /><br />
                En el tratamiento propuesto debe quedar muy claro que nunca podremos eliminar una
                cicatriz, aunque s intentaremos mejorarla. Las tcnicas usadas dependen de los
                casos pero las principales son:
                <br /><br />
                <ul>
                  <li>
                    Revisin quirrgica: consiste en su extirpacin y nueva sutura. Debe valorarse
                    cambiar la forma, direccin o la ubicacin de la cicatriz para favorecer el nuevo
                    proceso cicatricial
                  </li>
                  <li>
                    Presoterapia: consiste en la aplicacin de compresin sobre la cicatriz. La
                    presin mantenida disminuye la vascularizacin de la cicatriz y favorece el
                    remodelado del colgeno. Se utilizan prendas hechas a medida en la ortopedia
                    y deben usarse un mnimo de 16 horas al da
                  </li>
                  <li>
                    Lminas y cremas de silicona: de mecanismo desconocido, controlan la fase
                    inflamatoria cicatricial
                  </li>
                  <li>
                    Corticoides: en forma de cremas tpicas o por infiltracin en la propia
                    cicatriz. Bloquean la respuesta inflamatoria. Pueden causar atrofia cutnea
                    y la aparicin de capilares dilatados, por lo que se aplicarn con prudencia
                  </li>
                  <li>
                    Radioterapia local: puede plantearse en casos seleccionados para tratar los
                    queloides cuando el resto de tratamientos no han conseguido resultados
                  </li>
                </ul>
                <br /><br />
                En muchas ocasiones el tratamiento de una cicatriz puede suponer la combinacin
                juiciosa de varias de estas tcnicas.
              </p>
            </div>
