import React from 'react';
import './aumento_menton.css';

import { Link } from 'react-scroll';

import bg_img from '../../../../assets/img/bg_blefaroplastia.jpg';
import { Overlay, Logo, Navbar, NavbarFull, Footer, NavDotsFacial } from '../../../../components';

const Aumento_Menton = () => {
  return (
    <div className='info__menton'>
      <div>
        <Navbar />
        {/* <NavbarFull /> */}
        <Logo />
        
        <div className='content__overlay__menton'>
          <div className='content__container__menton'>
            <div className='content__info__menton'>
              <h1>Mentn</h1>
              <p>
                La barbilla constituye la parte inferior ms prominente del perfil facial. Su
                forma depende fundamentalmente del mentn, esto es, de aquella parte del hueso
                de la mandbula comprendida entre los dos dientes caninos. Pero tambin influyen
                todas las partes blandas que lo recubren (msculo, grasa y piel).
                <br /><br />
                Un mentn bien desarrollado es importante para mostrar un perfil facial
                proporcionado, manteniendo unas relaciones adecuadas con la nariz y con el cuello.
                <br /><br />
                La falta de desarrollo del mentn se llama, tcnicamente, microgenia y es el
                problema que nos interesa aqu. Adems de causar un perfil no favorable, es una
                causa predisponente al descolgamiento de los tejidos del cuello con el paso de los
                aos, generando un cuello obtuso.
                <br /><br />
                No debemos confundir la microgenia con la retrognatia, problema ms complejo que
                afecta a toda la mandbula y no slo al mentn. Aunque externamente pueden parecerse,
                la diferencia fundamental es que en la retrognatia existe tambin una alteracin en
                el contacto de los dientes entre s (malaoclusin dental), pues la arcada dentaria
                inferior es proporcionalmente ms pequea que la superior. Su solucin es compleja y
                exige elongacin del hueso de la mandbula mediante tcnicas de ciruga ortogntica
                y ortodoncia.
              </p>

              <h2>Cmo se aumenta el mentn?</h2>
              <p>
                No hay una sola tcnica para conseguir nuestro objetivo. 
                <br /><br />
                Las posiblidades son:
                <br /><br />                
                <ul>
                  <Link to='implante-grasa'><li>Aumento de mentn con implante de grasa del propio paciente</li></Link>
                  <Link to='implante-relleno'><li>Aumento de mentn con implantes de relleno inyectables</li></Link>
                  <Link to='implante-solido'><li>Aumento de mentn con implante slido (prtesis)</li></Link>
                  <Link to='osteotomia-mandibular'><li>Aumento de mentn por osteotoma mandibular</li></Link>
                </ul>
                <br /><br />
                En las tres primeras introducimos un implante para realzar su proyeccin.
                En la ltima se provoca una fractura controlada del mentn para cambiar su posicin.
                <br /><br />
                Se indicar un tipo de tratamiento u otro dependiendo de su caso particular.
              </p>

              <h2 id='implante-grasa'>Aumento del mentn con implante de grasa del propio paciente</h2>
              <p>
                Es una tcnica laboriosa que utiliza como implante la grasa del propio paciente.
                sta es su principal ventaja, pues el implante siempre es bien tolerado por el
                individuo y los resultados son muy naturales.
                <br /><br />
                Puede hacerse con anestesia local y sedacin y no requiere ingreso en clnica.
                <br /><br />
                En la primera parte de la ciruga obtenemos el implante de grasa mediante
                liposuccin de otra zona corporal. Tras procesarla y concentrarla mediante
                diferentes tcnicas, se inyecta en la zona elegida mediante cnula fina a travs
                de mnimas incisiones cutneas. 
                <br /><br />
                La grasa infiltrada se comporta como un injerto vivo de tejido, es decir, que para
                que el implante tenga xito debe vascularizarse. En caso contrario la grasa se
                reabsorber, perdiendo el efecto conseguido.
                <br /><br />
                ste es el principal problema de esta tcnica. Se estima que aproximadamente el
                30-50% de la grasa implantada podra reabsorberse por lo que, para llegar a un
                resultado satisfactorio, pueden ser necesarias nuevas sesiones de relleno.
              </p>

              <h2 id='implante-relleno'>Aumento del mentn con implantes de relleno inyectables</h2>
              <p>
                En este caso, se usa como material de relleno implantes sintticos inyectables. 
                <br /><br />
                Son formulados y fabricados en un laboratorio y dispensados en forma de jeringuillas
                para su aplicacin por puncin con aguja fina. Existen muchas marcas comerciales.
                Se diferencian entre s por su composicin qumica y su duracin ms o menos
                prolongada en el tiempo (reabsorbibles o permanentes).  
                <br /><br />
                Su cirujano plstico le asesorar sobre cul es el implante ms adecuado para su caso.
                <br /><br />
                Esta tcnica es la ms sencilla de las descritas. Se practica con anestesia local en
                la consulta y su aplicacin no dura ms de 30 minutos. 
                <br /><br />
                Existe todo un captulo que desarrolla este tema que puede consultar en el apartado
                de Medicina Esttica.
              </p>

              <h2 id='implante-solido'>Aumento del mentn con implantes slidos (prtesis)</h2>
              <p>
                En esta tcnica se recurre al uso de una prtesis, esto es, un implante slido de
                un material artificial, colocado justo encima del hueso del pmulo, para aumentar
                su relieve.
                <br /><br />
                Por su composicin, exiten mucho tipos de implantes. Quizs el ms clsico sea la
                silicona slida (polmero inorgnico a base de slice). Otros ejemplos son el
                politetrafluoroetileno expandido (Gore-tex) y el polietileno (Medpor).
                <br /><br />
                Tambin existen una multitud de modelos que varan entre s segn su consistencia,
                su forma, su tamao y la proyeccin que generan.
                <br /><br />
                Su cirujano plstico le asesorar sobre cul es el implante ms adecuado para su
                caso.
                <br /><br />
                La colocacin de este tipo de prtesis supone una pequea intervencin quirrgica.
                Habitualmente se practica bajo anestesia general y el paciente permanecer ingresado
                en la clnica la primera noche. En casos seleccionados puede realizarse con anestesia
                local y sedacin.
                <br /><br />
                Normalmente colocamos el implante por va externa, a travs de la piel, colocando la
                incisin justo detrs de la barbilla, quedando siempre en un lugar poco visible.
                Basta con una incisin de pocos centmetros de longitud para poder introducir una
                prtesis de tamao mediano.
                <br /><br />
                Est descrita tambin la va interna, intraoral, colocando la incisin en la unin
                del labio inferior con la enca, pero tiene mayor riesgo de infeccin y de
                dehiscencia de la herida.
              </p>

              <h2 id='osteotomia-mandibular'>Aumento del mentn por osteotoma mandibular</h2>
              <p>
                Es la nica tcnica en la que se consigue aumentar el mentn sin introducir
                implante alguno.
                <br /><br />
                Es la ms compleja de todas. Requiere anestesia general y una noche de ingreso
                en clnica.
                <br /><br />
                Mediante un abordaje intraoral amplio, colocando la incisin entre el labio
                inferior y la enca, se expone la parte anterior de la mandbula entre ambos
                dientes caninos. Con una sierra oscilante se hace un corte transversal del mentn
                para separar esta porcin de hueso del resto de la mandbula y poder as cambiar
                su posicin. Se debe fijar mediante miniplacas irreabsorbibles de titanio para que
                suelde correctamente en su nueva ubicacin, como cualquier fractura de un hueso.
                <br /><br />
                El riesgo principal de esta tcnica es la lesin de los nervios mentonianos, que
                dan la sensibilidad de la piel del labio inferior y mentn.
              </p>

              <h2>Limitaciones del aumento del mentn</h2>
              <p>
                El aumento de mentn no puede corregir todos los problemas de esta regin de la
                mandbula. 
                <br /><br />
                Aunque tensa la barbilla, este tratamiento no cambia la calidad o textura de la
                piel, por lo que este factor puede influir en el resultado final de la tcnica
                usada.
                <br /><br />
                Tampoco tiene influencia alguna sobre la forma en la que contactan entre s los
                dientes (oclusin dental) ni sobre el tamao general de la mandbula.
                <br /><br />
                Los implantes pueden llegar a ser visibles o palpables como postizos debajo de
                la piel. Pueden no quedar perfectamente fijados en su localizacin y sufrir
                desplazamientos secundarios, causando cambios en el resultado inicialmente obtenido.
                <br /><br />
                Algunos tipos de implantes se van a ir reabsorbiendo poco a poco, pudiendo perder
                parte de la proyeccin original alcanzada y precisando de nuevas sesiones de
                tratamiento para mantener los resultados originales. Esto ocurre con algunos
                implantes de relleno inyectables y tambin con parte de la grasa.
              </p>
            </div>
          </div>
        </div>

        <div className='footer__info__menton'>
          <div className='footer__info_site__menton'>
              <ul>
                  <li>
                      <a href="/aviso-legal" className='underline'>Aviso Legal</a>
                  </li>
                  <li>-</li>
                  <li>
                      <a href="/politica-privacidad" className='underline'>Poltica de Privacidad</a>
                  </li>
                  <li>-</li>
                  <li>
                      <a href="/creditos" className='underline'>Crditos</a>
                  </li>
              </ul>
          </div>
        </div>

        <img className='bg__content_info' src={bg_img} />
      </div>
    </div>
  )
}

export default Aumento_Menton