import React from 'react';
import '../styles.css';


import bg_img from '../../../../assets/img/bg_blefaroplastia.jpg';
import { Overlay, Logo, Navbar, NavbarFull, Footer, NavDotsMamaria } from '../../../../components';

const Reduccion_Mama = () => {
    return (
        <div className='home'>
      <div className='home__bg'>
        <Navbar />
        {/* <NavbarFull /> */}
        <Logo />
        <Overlay />
        <Footer />

        <div className='home__content'>
          <NavDotsMamaria />
          <div className='content__section'>
            <h2 className='content__title'>Reduccin de Mamas</h2>
            <div className='main__content'>
              <p>
                La reduccin de mamas es una tcnica quirrgica que se indica cuando la paciente
                presenta una hipertrofia de sus mamas. Tambin recibe el nombre de mamoplastia
                de reduccin. 
                <br /><br />
                Aunque el objetivo inicial es reducir el tamao, tambin permite remodelar su
                forma y reducir el dimetro de la areola para hacerlo proporcional al nuevo tamao.
                Como en la mayora de ocasiones de hipertrofia la mama tambin est cada, la ciruga
                de reduccin mamaria debe elevarla a su posicin ideal para obtener un resultado
                satisfactorio.
                <br /><br />
                En general, esta ciruga se practicar cuando se haya completado el desarrollo
                mamario, hacia los 18 aos de edad.
              </p>
              <a href="/cirugia-estetica/cirugia-mamaria/reduccion-mamas"><h3>Ms informacin</h3></a>
            </div>
          </div>
        </div>
        <img className='bg__img' src={bg_img} />
      </div>
    </div>
    );
}

export default Reduccion_Mama