import React from 'react';
import '../styles.css';


import bg_img from '../../../../assets/img/bg_blefaroplastia.jpg';
import { Overlay, Logo, Navbar, NavbarFull, Footer, NavDotsCorporal } from '../../../../components';

const Aumento_Pomulos = () => {
  return (
    <div className='home'>
      <div className='home__bg'>
        <Navbar />
        {/* <NavbarFull /> */}
        <Logo />
        <Overlay />
        <Footer />
        <div className='home__content'>
          <NavDotsCorporal />
          <div className='content__section'>
            <h2 className='content__title'>Dermolipectoma en cinturn</h2>
            <div className='main__content'>
              <p>
                La dermolipectoma en cinturn es un procedimiento quirrgico que elimina el exceso de
                piel y de tejido graso subcutneo de la mitad inferior del tronco, tanto por delante como
                por detrs, es decir, de aquella rea ubicada a nivel del cinturn de la ropa. Tambin
                puede ayudar a tensar los msculos de la pared abdominal.
                <br /><br />
                Normalmente est indicada en casos de descolgamientos muy importantes, tales como los que
                se ven tras adelgazamientos severos como los ocurridos tras las tcnicas de by-pass gstrico
                para el tratamiento de grandes obesidades.
                <br /><br />
                No existe un tipo universal de dermolipectoma en cinturn que cubra las necesidades de
                todos los pacientes. La tcnica quirrgica se disea para cada paciente, dependiendo de
                su caso particular.
              </p>
              <a href="/cirugia-estetica/cirugia-corporal/dermolipectomia-cinturon"><h3>Ms informacin</h3></a>
            </div>
          </div>
        </div>
        <img className='bg__img' src={bg_img} />
      </div>
    </div>
  )
}

export default Aumento_Pomulos