import React from 'react';
import './lifting.css';


import bg_img from '../../../../assets/img/bg_blefaroplastia.jpg';
import { Overlay, Logo, Navbar, NavbarFull, Footer, NavDotsFacial } from '../../../../components';

const Lifting = () => {
  return (
    <div className='info__lifting'>
      <div>
        <Navbar />
        {/* <NavbarFull /> */}
        <Logo />
        
        <div className='content__overlay__lifting'>
          <div className='content__container__lifting'>
            <div className='content__info__lifting'>
              <h1>Lifting de cara y cuello</h1>
              <p>
                El lifting es un procedimiento quirrgico cuyo objetivo primario es
                eliminar el descolgamiento de los tejidos de la cara y el cuello,
                dndoles nueva tensin. Tambin recibe el nombre de ritidectoma.
                <br /><br />
                Tambin es una tcnica que puede mejorar la hiperactividad de la
                musculatura a nivel del tercio superior de la cara y del cuello,
                mejorando las arrugas de expresin a este nivel.
                <br /><br />
                No existe un tipo universal de ritidectoma que cubra las necesidades
                de todos los pacientes. La tcnica quirrgica se disea para cada
                paciente, dependiendo de su caso particular. Su cirujano le asesorar
                sobre qu tcnica es la ms adecuada en su caso concreto.
                <br /><br />
                No hay una edad definida para indicar esta ciruga. Se valorar
                individualmente la necesidad o no de indicar esta ciruga.
                <br /><br />
                El mejor candidato para un lifting es aquella persona con un grado
                moderado de descolgamiento pero cuya piel tiene elasticidad y cuya
                estructura sea est bien definida.
              </p>

              <h2>Lifting frontal</h2>
              <p>
                El lifting frontal se disea para tratar el tercio superior de la cara
                (frente y cejas). 
                <br /><br />
                La cicatriz clsica se extiende entre el pelo, a modo de diadema, entre
                oreja y oreja. En casos seleccionados, el abordaje puede seguir la lnea
                de implantacin del pelo de la cabeza. Cada vez se tiende a hacer abordajes
                ms limitados, practicando toda la ciruga a travs de varias incisiones
                menores colocadas entre el pelo, incluso auxiliados por endoscopia.
                <br /><br />
                Sus objetivos son el estiramiento de la piel frontal y de las cejas, as
                como tambin la correccin de sus arrugas de expresin (horizontales en
                la frente, verticales en el entrecejo).
              </p>

              <h2>Lifting ciliar</h2>
              <p>
                El lifting ciliar se disea para tratar slo la cada de las cejas. 
                <br /><br />
                Pueden usarse abordajes quirrgicos comunes al lifting frontal (entre el
                pelo o en la lnea de implantacin pilosa) o practicarse incisiones siguiendo
                el borde superior de la propia ceja (lifting ciliar directo) o alguna arruga
                profunda que se marque en la frente.
                <br /><br />
                Los intentos de elevar la ceja desde el prpado superior tienen resultados
                limitados.
                <br /><br />
                Su objetivo primario es elevar las cejas y, el secundario, cambiar su forma.
                La expresin facial es muy sensible a los cambios en la posicin y forma
                ciliares.
              </p>

              <h2>Lifting del tercio medio facial</h2>
              <p>
                El lifting del tercio medio facial se practica para tratar la zona central
                de la mejilla.
                <br /><br />
                Cuando se indica exclusivamente este lifting, el abordaje quirrgico sigue el
                borde del prpado inferior y puede auxiliarse con alguna incisin entre el pelo
                e, incluso, por dentro de la boca.
                <br /><br />
                Su objetivo principal es reposicionar la grasa subcutnea de la zona del pmulo
                y tensar los tejidos del prpado inferior.
                <br /><br />
                Cada vez se le da ms importancia a este tipo de lifting por la repercusin en
                el rejuvenecimiento facial que consigue.
              </p>

              <h2>Lifting cervicofacial</h2>
              <p>
                El lifting cervicofacial constituye el lifting clsico.
                <br /><br />
                Diseado para tratar la zona lateral de la mejilla, el reborde de la mandbula y
                la regin del cuello.
                <br /><br />
                Su incisin tiene forma de letra "U", con un componente vertical que, empezando
                entre el pelo de la sien desciende por delante de la oreja, rodea el lbulo y
                vuelve a ascender por el surco retroauricular, acabando a distintos niveles entre
                el pelo de la nuca.
                <br /><br />
                De forma frecuente, se debe practicar tambin una incisin debajo del mentn para
                poder tratar la porcin ms anterior del cuello.
                <br /><br />
                Su objetivo principal es tensar los tejidos de la mejilla, definir mejor el reborde
                mandibular, mejorar el ngulo entre la mandbula y el cuello y tratar de eliminar
                las bandas verticales cervicales formadas por la contraccin de su msculo.
                <br /><br />
                Una variante ms conservadora del lifting cervicofacial, cuando la redundancia de
                piel es limitada, es el llamado <b>Minimal Acces Cranial Suspension Lift (MACS lift)</b>,
                que a travs de un abordaje slo preauricular, extendido alrededor de la patilla
                del pelo, consigue elevar los tejidos de la mejilla y del cuello mediante unas
                suturas de disposicin vertical.
              </p>

              <h2>Limitaciones del lifting</h2>
              <p>
                No todos los factores de los que depende el aspecto y forma de la cara y el
                cuello pueden ser corregidos con esta ciruga:
                <br /><br />
                <ul>
                  <li>
                    La ritidectoma no alcanza a modificar la zona central facial.
                    Por lo tanto, los prpados, la nariz y los labios continuarn con el
                    mismo aspecto.
                  </li>
                  <li>
                    Tampoco cambia la calidad de la piel ni su elasticidad. No corrige su
                    atrofia ni sus estigmas asociados (manchas seniles, sequedad, arrugas
                    finas). Puede precisar tratamientos adicionales (peeling, lser, etc)
                    para su tratamiento especfico.
                  </li>
                  <li>
                    Aunque mejoran, las arrugas de expresin (dependientes de una
                    hiperactividad muscular) pueden no desaparecer totalmente con la
                    ritidectoma.
                  </li>
                  <li>
                    Tampoco puede cambiar la estructura sea facial ni el grosor de la
                    grasa subcutnea. Pueden ser necesarios asociar el lifting a la
                    introduccin de diversos implantes para mejorarlos.
                  </li>
                  <li>
                    Esta ciruga no detiene el proceso de envejecimiento. Pueden ser
                    necesarias cirugas futuras para mantener los resultados.
                  </li>
                </ul>
              </p>
            </div>
          </div>
        </div>

        <div className='footer__info__lifting'>
          <div className='footer__info_site__lifting'>
              <ul>
                  <li>
                      <a href="/aviso-legal" className='underline'>Aviso Legal</a>
                  </li>
                  <li>-</li>
                  <li>
                      <a href="/politica-privacidad" className='underline'>Poltica de Privacidad</a>
                  </li>
                  <li>-</li>
                  <li>
                      <a href="/creditos" className='underline'>Crditos</a>
                  </li>
              </ul>
          </div>
        </div>

        <img className='bg__content_info' src={bg_img} />
      </div>
    </div>
  )
}

export default Lifting