import React from 'react';
import './hilos.css'

import { Link } from 'react-scroll';

import bg_img from '../../../../assets/img/bg_blefaroplastia.jpg';
import { Overlay, Logo, Navbar, NavbarFull, Footer, NavDotsFacial } from '../../../../components';

const Hilos = () => {
  return (
    <div className='info__hilos'>
      <div>
        <Navbar />
        {/* <NavbarFull /> */}
        <Logo />
        
        <div className='content__overlay__hilos'>
          <div className='content__container__hilos'>
            <div className='content__info__hilos'>
              <h1>Hilos tensores</h1>
              <p>
                Un hilo tensor es el nombre genrico que reciben todos aquellos hilos
                diseados para ser insertados debajo de la piel y que se indican para
                mejorar la flaccidez de los tejidos.
                <br /><br />
                Con su implantacin, la respuesta natural del organismo consiste en la
                activacin de una reaccin inflamatoria local pues el hilo es reconocido
                como un cuerpo extrao. Esto provoca el depsito de fibras de colgeno a lo
                largo del trayecto del hilo (fibrosis). Como el colgeno es una fibra con
                capacidad de retraccin, es decir, se acorta en su longitud, se consigue un
                efecto tensor biolgico que mejora la flaccidez tisular. 
                <br /><br />
                Como la fabricacin y el depsito del colgeno es un fenmeno lento, el
                efecto tensor empezar a notarse a partir del mes y no ser estable hasta
                transcurridos unos tres meses desde la colocacin del hilo.
                <br /><br />
                Algunos tipos de hilos se han modificado para conseguir un efecto tensor
                ms intenso e inmediato, gracias a aadir unos puntos de anclaje a lo largo
                de su longitud que van a actuar como pequeos anzuelos que se irn enganchado
                eficazmente al tejido a lo largo de su recorrido, provocando as de forma
                inmediata un <b>efecto tensor mecnico</b>, que se aprecia ya justo tras su colocacin.
              </p>

              <h2>Qu tipos de hilos tensores existen?</h2>
              <p>
                Aunque siempre estn fabricados con materiales biocompatibles, su estructura
                y composicin son variables de unos tipos a otros. 
                <br /><br />
                Se diferencian entre s por el tipo de material con el que se fabrican
                (PDO o polidioxanona, cido polilctico, caprolactona, polipropilieno, oro),
                por su duracin (hilo reabsorbible o irreabsorbible) y segn el nmero de
                hebras que poseen (monofilamentos y polifilamentos).
                <br /><br />
                En el caso de los hilos con puntos de anclaje, estos pueden adoptar diferentes
                formas (espiculas, barbas, conos) y disposicin (unidireccional, bidireccional,
                helicoidal). 
                <br /><br />
                Segn el tipo de hilo y la tcnica de colocacin, destacan los siguientes:
                <br /><br />
                <ul>
                  <li>
                    Hilos de oro: son suturas permanentes lisas a base de oro. Una marca
                    comercial es el Skin Gold
                  </li>
                  <li>
                    Hilos mgicos: suturas temporales lisas de PDO. Su tcnica de aplicacin
                    se conoce popularmente como el lifting japons o coreano, pues es muy
                    popular su uso en Asia. Marcas comerciales son el V-LIFT (monofilamento
                    de PDO) y el DW (polifilamento de PDO)
                  </li>
                  <li>
                    Hlos APTOS: llamados tambin Hilos Rusos, fueron introducidos por el Dr
                    Salamanidze. Fueron los primeros hilos con puntos de anclaje usados, que
                    adoptan la forma de espcula. Una marca comercial es el Happy Lift
                  </li>
                  <li>
                    Suturas Silhouette: son hilos con puntos de enclaje en forma de cono.
                    Hay una variante permanente (Silhouette Lift) y otra reabsorbible
                    (Shilouette Soft)
                  </li>
                </ul>
              </p>

              <h2>Indicaciones de los hilos tensores</h2>
              <p>
                La colocacin de los hilos tensores est indicada especialmente para la
                correccin de los descolgamientos leves a moderados de los tejidos.
                <br /><br />
                A nivel facial, estn especialmente diseados para elevar la cola de la
                ceja y corregir la flaccidez de la mejilla. Tambin permiten mejorar el
                descolgamiento del cuello y definir mejor el reborde mandibular.
                <br /><br />
                Se han aplicado los hilos para mejorar la flaccidez de otras zonas, como
                la cara interna de brazos y muslos, las mamas o el abdomen, con resultados
                variables.
              </p>

              <h2>Cmo se colocan los hilos tensores?</h2>
              <p>
                En condiciones normales, este tratamiento se realiza en la consulta.
                <br /><br />
                Es fundamental comenzar definiendo las reas a tratar y establecer la direccin
                en la que se introducirn los hilos, segn el objetivo a conseguir. Se definen
                as diferentes patrones de insercin. Habitualmente se insertan varios hilos
                por lado pues difcilmente un solo hilo consigue el resultado deseado. Cuando
                usamos hilos sin puntos de anclaje pueden ser necesarios implantar 10-20
                hilos/lado, formando una retcula o red. En cambio, cuando usamos hilos
                con puntos de anclaje, pueden bastar con 2-3 hilos/lado siguiendo las lneas
                de traccin deseadas.
                <br /><br />
                Despus de desinfectar la zona, se aplica anestesia local en los puntos de
                entrada y salida de los hilos. 
                <br /><br />
                Cuando usamos hilos sin puntos de anclaje, su insercin se realiza con la
                ayuda de agujas de fino calibre que estn enhebradas en sus extremos. Por
                lo tanto, el tratamiento se hace percutneamente, por puncin simple.
                <br /><br />
                Cuando usamos hilos con puntos de anclaje, despus de su introduccin se
                aplica traccin en sus extremos para conseguir que el hilo se enganche en el
                tejido y conseguir as un efecto de tensin mecnica inmediata en los tejidos.
                En casos seleccionados, el punto de entrada del hilo puede ampliarse con un
                pequeo corte con el bistur si queremos suturar el extremo del hilo a los
                tejidos profundos, mejorando as su anclaje y, por tanto, su tensin. 
              </p>

              <h2>Contraindicaciones de los hilos tensores</h2>
              <p>
                En la primera consulta el cirujano realizar una historia clnica completa,
                descartando enfermedades generales o medicaciones que est tomando que
                pudieran interferir o incluso contraindicar el tratamiento.
                <br /><br />
                El uso de los hilos tensores, como el de cualquier otro implante, est
                contraindicado en los siguientes casos:
                <br /><br />
                <ul>
                  <li>
                    Sensibilidad conocida a algunos de sus componentes.
                  </li>
                  <li>
                    Si existen enfermedades de la coagulacin sangunea como la hemofilia o
                    toma medicamentos que la alteran (cido acetilsaliclico -Aspirina - ,
                    heparina, Sintrom, Plavix  y otros).
                  </li>
                  <li>
                    Infecciones activas de la piel en la zona a tratar.
                  </li>
                  <li>
                    Antecedentes de enfermedades conocidas del sistema inmunitario en fase
                    activa o pacientes con tratamiento farmacolgico inmunosupresor.
                  </li>
                </ul>
                <br /><br />
                Se desconocen las posibles interacciones de este producto con otros implantes
                cutneos que el paciente pudiera tener previamente aplicados en la zona a
                tratar. No se aconseja, por tanto, mezclar productos en la misma rea.
              </p>

              <h2>Limitaciones de los hilos tensores</h2>
              <p>
                El proceso de envejecimiento no se detiene con la insercin de los hilos
                tensores.
                <br /><br />
                Adems, las suturas pueden no corregir completamente la flaccidez de los
                tejidos.Con el paso del tiempo, los hilos insertados van perdiendo de forma
                lenta y progresiva su efecto tensor. Los primeros resultados apuntan a que
                el resultado se pierde tras los primeros 12-18 meses.
              </p>
            </div>
          </div>
        </div>

        <div className='footer__info__hilos'>
          <div className='footer__info_site__hilos'>
              <ul>
                  <li>
                      <a href="/aviso-legal" className='underline'>Aviso Legal</a>
                  </li>
                  <li>-</li>
                  <li>
                      <a href="/politica-privacidad" className='underline'>Poltica de Privacidad</a>
                  </li>
                  <li>-</li>
                  <li>
                      <a href="/creditos" className='underline'>Crditos</a>
                  </li>
              </ul>
          </div>
        </div>

        <img className='bg__content_info' src={bg_img} />
      </div>
    </div>
  )
}

export default Hilos