import React from 'react';
import '../styles.css';


import bg_img from '../../../../assets/img/bg_blefaroplastia.jpg';
import { Overlay, Logo, Navbar, NavbarFull, Footer, NavDotsMamaria } from '../../../../components';

const Elevacion_Protesis = () => {
    return (
        <div className='home'>
      <div className='home__bg'>
        <Navbar />
        {/* <NavbarFull /> */}
        <Logo />
        <Overlay />
        <Footer />

        <div className='home__content'>
          <NavDotsMamaria />
          <div className='content__section'>
            <h2 className='content__title titulo__especial'>Elevacin de mamas con prtesis de relleno</h2>
            <div className='main__content'>
              <p className='content__especial'>
                La elevacin de mamas con prtesis de relleno es la tcnica quirrgica indicada para
                corregir, en una sola intervencin, una mama cada y de pequeo tamao. Recibe tambin
                el nombre de mastopexia con prtesis de relleno.
                <br /><br />
                Ofrece la posibilidad de elevar la mama al tiempo que conseguimos un aumento de su
                volumen mediante la insercin de una prtesis de relleno.
                <br /><br />
                En general, esta ciruga se practicar cuando se haya completado el desarrollo mamario,
                hacia los 18 aos de edad.
              </p>
              <a href="/cirugia-estetica/cirugia-mamaria/elevacion-mamas-protesis"><h3>Ms informacin</h3></a>
            </div>
          </div>
        </div>
        <img className='bg__img' src={bg_img} />
      </div>
    </div>
    );
}

export default Elevacion_Protesis