import React from 'react';
import './aumento_mamas.css';


import bg_img from '../../../../assets/img/bg_blefaroplastia.jpg';
import { Overlay, Logo, Navbar, NavbarFull, Footer, NavDotsMamaria } from '../../../../components';

const Aumento_Mama = () => {
  return (
    <div className='info__amamas'>
      <div>
        <Navbar />
        {/* <NavbarFull /> */}
        <Logo />

        <div className='content__overlay__amamas'>
          <div className='content__container__amamas'>
            <div className='content__info__amamas'>
              <h1>Aumento de mamas</h1>
              <p>
                Una mama pequea puede aparecer en dos situaciones bsicas:
                <br /><br />
                <ul>
                  <li>
                    Por falta de desarrollo durante la pubertad. Es la llamada hipoplasia
                    mamaria, que es un problema constitucional en la mayora de casos.
                  </li>
                  <li>
                    Por involucin a lo largo de la vida de la paciente, condicionada
                    bsicamente por los cambios hormonales propios de la mujer. Es la
                    llamada atrofia mamaria, tpica por ejemplo tras el embarazo.
                  </li>
                </ul>
                <br /><br />
                De todas formas, el tamao ideal de las mamas no es un valor absoluto.
                Debe considerarse siempre la complexin fsica de la mujer. Adems, hay
                factores raciales, culturales, personales e incluso modas que condicionan
                este concepto.
              </p>

              <h2>Cmo se aumentan las mamas?</h2>
              <p>
                No existe una nica tcnica de aumento de mamas que cubra las necesidades
                de todos los pacientes. Todas ellas tienen en comn que se precisa de la
                introduccin de un implante para lograr nuestro objetivo.
                <br /><br />
                Las posiblidades son:
                <br /><br />
                <ul>
                  <li>Aumento de mamas con implante slido (prtesis).</li>
                  <li>Aumento de mamas con implante de grasa del propio paciente.</li>
                </ul>
                <br /><br />
                Se indicar un tipo de tratamiento u otro dependiendo de su caso particular.
              </p>

              <h2>Aumento de mamas con prtesis</h2>
              <p>
                Una prtesis es un implante artificial que consiste en una cubierta o bolsa rellenada de diversas sustancias. 
                <br /><br />
                Existen muchos modelos de prtesis, clasificados segn:
                <br /><br />
                <ul>
                  <li>
                    El relleno:
                    <ul>
                      <li>
                        Silicona: es el ms frecuente y consiste en un polmero inorgnico a base
                        de slice, que ha demostrado ser uno de los materiales mejor tolerados por
                        el organismo. Su consistencia es gelatinosa para remedar la de una mama natural.
                        Las prtesis actuales tienen un gel ms cohesivo que las originales, evitando
                        la migracin de partculas de silicona en caso de rotura del implante.
                      </li> 
                      <li>
                        Suero fisiolgico: es una solucin de agua con sal. La consistencia es de
                        lquido, siendo menos natural al tacto que la silicona.
                      </li>
                      <li>
                        Otros: raramente usados actualmente (rellenos de hidrogel, de aceite, etc).
                      </li>
                    </ul>
                  </li>
                </ul>
                <br /><br />
                <ul>
                  <li>
                    Tipo de cubierta:
                    <ul>
                      <li>
                        Silicona: la mayora de prtesis tienen una cubierta slida consistente
                        en silicona en forma de elastmero.
                      </li>
                      <li>
                        Poliuretano: algunas prtesis tienen esta cubierta externa adicional
                        con el objetivo de disminuir el riesgo de una contractura capsular.
                      </li>
                    </ul>
                  </li>
                </ul>
                <br /><br />
                <ul>
                  <li>
                    La rugosidad de la superficie externa de la cubierta:
                    <ul>
                      <li>
                        Ausente (superficie lisa): la clsica.
                      </li>
                      <li>
                        Presente (superficie rugosa o texturizada): son las ms usadas actualmente,
                        por menor riesgo de contractura de la cpsula.
                      </li>
                    </ul>
                  </li>
                </ul>                
                <br /><br />
                <ul>
                  <li>
                    La forma del implante (visin de frente):
                    <ul>
                      <li>
                        Prtesis redondas: misma dimensin vertical que horizontal.
                      </li>
                      <li>
                        Prtesis anatmicas: diferente dimensin vertical que horizontal,
                        para adaptarse mejor a la conformacin del trax de la mujer (ancho,
                        estndar, largo). En general, tienen forma de "lgrima" o de "pera" 
                        pues estn ms rellenas en su parte inferior con el objetivo de conseguir
                        una forma ms natural o madura a la mama.
                      </li>
                    </ul>
                  </li>
                </ul>
                <br /><br />
                <ul>
                  <li>
                    La proyeccin del implante (visin de perfil):
                    <ul>
                      <li>Proyeccin baja.</li>
                      <li>Proyeccin moderada.</li>
                      <li>Proyeccin alta.</li>
                    </ul>
                  </li>  
                </ul>
              </p>

              <h2>Cmo se implantan las prtesis de mama?</h2>
              <p>
                En general se realiza bajo anestesia general y se recomienda un ingreso en
                clnica de 24 horas.
                <br /><br />
                Habitualmente, es suficiente una incisin en la piel de 4-5 cm de longitud
                para poder introducir una prtesis de tamao mediano. Se ubicar de forma
                que quede lo ms disimulada posible. Existen varias vas de abordaje posibles:
                <br /><br />
                <ul>
                  <li>
                    Axilar, hacindola coincidir con un pliegue cutneo natural, de forma que
                    con el brazo a lo largo del cuerpo la cicatriz queda oculta.
                  </li>
                  <li>
                    Areolar, siguiendo su permetro (donde acaba su color). Suele bastar incindir
                    slo la mitad inferior de su contorno.
                  </li>
                  <li>
                    Pliegue submamario, siguiendo el surco natural de la mama.
                  </li>
                </ul>
                <br /><br />
                Una vez en su interior, las prtesis puede quedar alojadas a diferentes niveles.
                De ms superficial a ms profundo distinguimos los siguientes planos anatmicos:
                <br /><br />
                <ul>
                  <li>
                    Subglandular: la prtesis se aloja justo debajo de la glndula mamaria.
                  </li>
                  <li>
                    Subfascial: la prtesis se cubre por la glndula y por la fascia muscular.
                  </li>
                  <li>
                    Subpectoral: la prtesis est en el nivel ms profundo, alojndose debajo
                    del msculo pectoral, apoyada sobre el plano costal.
                  </li>
                </ul>
              </p>

              <h2>Complicaciones de las prtesis de mama</h2>

              <p>
                Aunque potencialmente pueden existir diversos problemas relacionados con su uso,
                destacaramos los siguientes:
                <br /><br />
                <h3>Contractura de la cpsula</h3>
                Alrededor del implante, cuando cura la herida interna, se forma una cicatriz.
                Consiste en una capa delgada de tejido fibroso que aparece como una bolsa que
                rodea a la prtesis. Es un fenmeno fisiolgico y siempre existe. A esta cicatriz
                interna se le llama cpsula.
                <br /><br />
                A veces, como cualquier otra cicatriz, la cpsula puede desarrollarse de forma
                exagerada (hipertrofia de la cicatriz), engrosndose y ajustndose excesivamente
                al implante. A este fenmeno se le llama contractura de la cpsula.
                <br /><br />
                Sus consecuencias son el aumento en la consistencia de la mama (deja de estar
                blanda) e incluso el cambio en su forma natural. A veces, la paciente puede
                referir molestias.
                <br /><br />
                Se desconoce su causa. La tasa de contractura capsular suele ser menor al 5%
                para un aumento de mamas de tipo esttico. Puede manifestarse al poco tiempo
                de la ciruga o al cabo de aos. Puede ser asimtrico, afectando de forma ms
                intensa a una mama.
                <br /><br />
                <h3>Interferencia con la mamografa</h3>
                Los implantes mamarios pueden dificultar la realizacin e interpretacin de una
                mamografa pues su imagen se superpone a la del tejido glandular. El grado de
                interferencia depende del tipo de prtesis (las prtesis de silicona son ms
                opacas que las de suero fisiolgico) y del plano anatmico donde estn ubicadas
                (los situados debajo del msculo permiten visualizar mejor la mama que los
                colocados debajo de la glndula).
                <br /><br />
                Usted debe informar al radilogo de la presencia de prtesis mamarias para que
                puedan realizarse los estudios mamogrficos adecuados. Una opcin es hacer
                mamografas especializadas, cambiando las proyecciones habituales para poder
                visualizar el problema a estudio (proyecciones de Eklund). Otras posibilidades
                son realizar otro tipo de estudios complementarios tales como una ecografa o
                una resonancia nuclear magntica.
                <br /><br />
                <h3>Posibilidad de rotura del implante mamario</h3>
                Los implantes mamarios, al igual que otros dispositivos mdicos, pueden fallar.
                A veces, como antecedente podemos recoger un traumatismo previo, pero no siempre
                su causa es aparente.
                <br /><br />
                Un implante roto puede manifestarse con una prdida de volumen de la mama
                (sobre todo notable con las prtesis de suero fisiolgico), cambios en su forma
                o notarse irregularidades en su superficie a la palpacin (bultos). En ocasiones,
                sin embargo, puede ser totalmente asintomtico y ser un hallazgo casual en el
                transcurso de una revisin de rutina.
                <br /><br />
                Si el implante roto es de suero fisiolgico su contenido es reabsorbido por el
                organismo. La silicona, sin embargo, no es biodegradable y su contenido
                normalmente queda retenido dentro de la cpsula. En el caso de migrar fuera de
                ella quedarn alojados en forma de quistes en el tejido mamario circundante o
                engrosando los ganglios de la axila (adenopatas). Las prtesis actuales, con
                un gel de silicona ms cohesivo, disminuyen la posibilidad de migracin de la
                silicona hacia los tejidos vecinos.
                <br /><br />
                Una prtesis daada o rota no puede ser reparada por lo que debe recambiarse o
                retirarse.
              </p>

              <h2>Aumento de mamas con grasa de la propia paciente</h2>

              <p>
                Es una tcnica laboriosa que utiliza como implante la grasa de la propia paciente.
                sta es su principal ventaja, pues el implante siempre es bien tolerado por la
                mujer y los resultados son muy naturales.
                <br /><br />
                En general se precisa anestesia general y se recomienda un ingreso en clnica de
                24 horas.
                <br /><br />
                En la primera parte de la ciruga obtenemos el implante de grasa mediante
                liposuccin de otra zona corporal. Tras procesarla y concentrarla mediante
                diferentes tcnicas, se inyecta en las mamas mediante cnula fina a travs de
                mnimas incisiones cutneas. 
                <br /><br />
                La grasa infiltrada se comporta como un injerto vivo de tejido, es decir, que
                para que el implante tenga xito debe vascularizarse. En caso contrario la grasa
                se reabsorber, perdiendo el efecto conseguido.
                <br /><br />
                ste es el principal problema de esta tcnica. Se estima que aproximadamente el
                30-50% de la grasa implantada podra reabsorberse por lo que, para llegar a un
                resultado satisfactorio, pueden ser necesarias nuevas sesiones de relleno. 
                <br /><br />
                Su uso para el aumento esttico de la mama ha sido cuestionado por algunos
                autores argumentando que el implante de grasa puede dar imgenes radiolgicas
                equvocas que se pueden confundir incluso con enfermedades malignas de la mama.
                Sin embargo, se acepta actualmente que mdicos radilogos experimentados pueden
                perfectamente diferenciarlos entre s, no contraindicando por lo tanto su uso.
              </p>

              <h2>Limitaciones del aumento de mamas</h2>
              <p>
                La forma y el tamao de las mamas previas a la ciruga pueden influir en el
                resultado final. Si las mamas no tienen el mismo tamao o forma antes de la
                ciruga es poco probable que sean completamente simtricas despus.
                <br /><br />
                En general, se aconsejarn tamaos proporcionados a la complexin fsica de
                la mujer. Tamaos exagerados darn resultados estticos ms artificiales.
                <br /><br />
                Cuando usamos prtesis, en mujeres delgadas o con mamas pequeas ser difcil
                camuflar totalmente el implante. En estos casos se suele aconsejar colocarlo
                por detrs del msculo pectoral. De todas formas, la prtesis puede ser palpable
                en su permetro y, en ocasiones, incluso perceptible visualmente.
                <br /><br />
                Cuando la prtesis se coloca debajo del msculo pectoral puede alterarse su
                forma en grado variable cuando se contrae fuertemente el mismo. Si usted ejercita
                habitualmente su msculo pectoral por motivos de trabajo o aficin (gimnasio)
                podra hacer aconsejable no colocar el implante debajo del msculo pectoral.
                <br /><br />
                El uso del implante de grasa presenta unos resultados muy naturales pero no es
                fcil conseguir volmenes elevados ni mantener un relleno apropiado en el polo
                superior de la mama. Adems, la paciente debe tener acmulos grasos extras en
                otras partes de su cuerpo para poder recolectarlos para sus mamas.
                <br /><br />
                No existe una intervencin ideal para cada caso. Cada mtodo de implantacin
                escogidos tiene sus ventajas e inconvenientes. Dialogue con su cirujano que le
                asesorar en su eleccin.
              </p>
            </div>
          </div>
        </div>

        <div className='footer__info__amamas'>
          <div className='footer__info_site__amamas'>
              <ul>
                  <li>
                      <a href="/aviso-legal" className='underline'>Aviso Legal</a>
                  </li>
                  <li>-</li>
                  <li>
                      <a href="/politica-privacidad" className='underline'>Poltica de Privacidad</a>
                  </li>
                  <li>-</li>
                  <li>
                      <a href="/creditos" className='underline'>Crditos</a>
                  </li>
              </ul>
          </div>
        </div>
        <img className='bg__content_info' src={bg_img} />
      </div>
    </div>
  );
}

export default Aumento_Mama