import React from 'react';
import '../styles.css';


import bg_img from '../../../../assets/img/bg_blefaroplastia.jpg';
import { Overlay, Logo, Navbar, NavbarFull, Footer, NavDotsFacial } from '../../../../components';

const Blefaroplastia = () => {
  return (
    <div className='home'>
      <div className='home__bg'>
        <Navbar />
        {/* <NavbarFull /> */}
        <Logo />
        <Overlay />
        <Footer />
        
        <div className='home__content'>
          <NavDotsFacial />
          <div className='content__section'>
            <h2 className='content__title'>Blefaroplastia</h2>
            <div className='main__content'>
              <p>
                Con la edad, los principales problemas de los prpados son su descolgamiento y su
                relajacin. El primero genera redundancia en la piel y la aparicin de pliegues
                que, en casos extremos, puede interferir con la vista cuando afecta al prpado
                superior. El segundo favorece la herniacin del contenido de las rbitas, haciendo
                que las bolsas de grasa protuyan hacia el exterior formando bultos debajo de los
                prpados.<br /><br />

                La blefaroplastia esttica es un procedimiento quirrgico cuyo fin es, bsicamente,
                mejorar el aspecto de los prpados deteriorado con el envejecimiento. Es diseada
                individualmente para cada paciente, dependiendo de sus necesidades particulares.
                Puede realizarse de forma aislada para los prpados superiores, inferiores o ambos.
              </p>
              <a href="/cirugia-estetica/cirugia-facial/blefaroplastia"><h3>Ms informacin</h3></a>
              
            </div>
          </div>
        </div>
        <img className='bg__img' src={bg_img} />
      </div>
    </div>
  )
}

export default Blefaroplastia